<template>
  <section class="py-16 bg-gradient-to-br from-blue-50 to-indigo-100">
    <div class="container mx-auto px-4">
      <!-- Header -->
      <div class="text-center mb-12">
        <h2 class="text-4xl font-bold text-gray-900 mb-4">
          {{ $t('subscription.chooseYourPlan') }}
        </h2>
        <p class="text-xl text-gray-600 max-w-2xl mx-auto">
          {{ $t('subscription.chooseYourPlanDesc') }}
        </p>
      </div>

      <!-- Authentication Warning -->
      <div v-if="!isAuthenticated" class="bg-yellow-50 border border-yellow-200 rounded-lg p-6 mb-8 max-w-2xl mx-auto">
        <div class="flex items-center">
          <svg class="w-6 h-6 text-yellow-600 mr-3" fill="currentColor" viewBox="0 0 20 20">
            <path fill-rule="evenodd" d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z" clip-rule="evenodd" />
          </svg>
          <div>
            <h3 class="text-lg font-semibold text-yellow-800">Authentication Required</h3>
            <p class="text-yellow-700">Please log in to subscribe to a plan.</p>
          </div>
        </div>
        <div class="mt-4">
          <router-link
            to="/login"
            class="inline-flex items-center px-4 py-2 bg-yellow-600 hover:bg-yellow-700 text-white font-semibold rounded-lg transition-colors"
          >
            <svg class="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M11 16l-4-4m0 0l4-4m-4 4h14m-5 4v1a3 3 0 01-3 3H6a3 3 0 01-3-3V7a3 3 0 013-3h7a3 3 0 013 3v1" />
            </svg>
            Go to Login
          </router-link>
        </div>
      </div>

      <!-- Plans Grid -->
      <div class="grid md:grid-cols-3 gap-8 max-w-6xl mx-auto">
        <div
          v-for="plan in plans"
          :key="plan.id"
          class="relative bg-white rounded-2xl shadow-lg hover:shadow-xl transition-all duration-300 transform hover:-translate-y-2"
          :class="plan.isPopular ? 'ring-2 ring-blue-500 scale-105' : ''"
        >
          <!-- Popular Badge -->
          <div
            v-if="plan.isPopular"
            class="absolute -top-4 left-1/2 transform -translate-x-1/2 bg-gradient-to-r from-blue-500 to-purple-600 text-white px-6 py-2 rounded-full text-sm font-semibold"
          >
            {{ $t('subscription.mostPopular') }}
          </div>

          <!-- Plan Header -->
          <div class="p-8 text-center">
            <h3 class="text-2xl font-bold text-gray-900 mb-2">
              {{ $t(`subscription.plans.${plan.id}.name`) }}
            </h3>
            <div class="mb-6">
              <span class="text-4xl font-bold text-gray-900">
                {{ plan.price === 0 ? $t('subscription.free') : plan.price }}
              </span>
              <span v-if="plan.price !== 0" class="text-gray-600 ml-2">
                {{ $t('subscription.egp') }}
              </span>
            </div>
            <p class="text-gray-600 mb-6">
              {{ $t(`subscription.plans.${plan.id}.description`) }}
            </p>
          </div>

          <!-- Features List -->
          <div class="px-8 pb-8">
            <ul class="space-y-4">
              <li
                v-for="feature in plan.features"
                :key="feature"
                class="flex items-center"
              >
                <svg
                  class="w-5 h-5 text-green-500 mr-3 flex-shrink-0"
                  fill="currentColor"
                  viewBox="0 0 20 20"
                >
                  <path
                    fill-rule="evenodd"
                    d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
                    clip-rule="evenodd"
                  />
                </svg>
                <span class="text-gray-700">{{ $t(`subscription.features.${feature}`) }}</span>
              </li>
            </ul>
          </div>

          <!-- Action Button -->
          <div class="px-8 pb-8">
            <button
              @click="startPlan(plan.id)"
              :disabled="loading"
              class="w-full py-3 px-6 rounded-lg font-semibold transition-all duration-300"
              :class="plan.isPopular
                ? 'bg-gradient-to-r from-blue-500 to-purple-600 text-white hover:from-blue-600 hover:to-purple-700'
                : 'bg-gray-100 text-gray-900 hover:bg-gray-200'"
            >
              <span v-if="loading" class="flex items-center justify-center">
                <svg class="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                  <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
                  <path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                </svg>
                {{ $t('subscription.processing') }}
              </span>
              <span v-else-if="!isAuthenticated">Login Required</span>
              <span v-else>
                {{ $t('subscription.startPlan') }}
              </span>
            </button>
          </div>
        </div>
      </div>

      <!-- Current Plan Status -->
      <div v-if="userSubscription" class="mt-12 text-center">
        <div class="bg-white rounded-lg shadow-md p-6 max-w-md mx-auto">
          <h3 class="text-xl font-semibold text-gray-900 mb-4">
            {{ $t('subscription.currentPlan') }}
          </h3>
          <div class="space-y-2">
            <p class="text-lg font-medium text-gray-700">
              {{ $t(`subscription.plans.${userSubscription.planId}.name`) }}
            </p>
            <p class="text-sm text-gray-600">
              {{ $t('subscription.status') }}: 
              <span :class="userSubscription.status === 'active' ? 'text-green-600' : 'text-red-600'">
                {{ $t(`subscription.statuses.${userSubscription.status}`) }}
              </span>
            </p>
            <p v-if="getRemainingDays > 0" class="text-sm text-gray-600">
              {{ $t('subscription.remainingDays', { days: getRemainingDays }) }}
            </p>
            <p v-else-if="userSubscription.planId === 'Basic'" class="text-sm text-green-600">
              {{ $t('subscription.permanent') }}
            </p>
          </div>
        </div>
      </div>

      <!-- View All Plans Link -->
      <div class="text-center mt-8">
        <router-link
          to="/subscriptions"
          class="inline-flex items-center text-blue-600 hover:text-blue-700 font-semibold"
        >
          {{ $t('subscription.viewAllPlans') }}
          <svg class="w-5 h-5 ml-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 5l7 7-7 7" />
          </svg>
        </router-link>
      </div>
    </div>
  </section>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import { useSubscriptions } from '@/composables/useSubscriptions'
import { useRouter } from 'vue-router'
import { getAuth, onAuthStateChanged } from 'firebase/auth'
import Swal from 'sweetalert2'

const router = useRouter()
const auth = getAuth()
const isAuthenticated = ref(false)

const {
  plans,
  userSubscription,
  loading,
  error,
  fetchPlans,
  fetchUserSubscription,
  startSubscription,
  getRemainingDays
} = useSubscriptions()

onMounted(async () => {
  // Check authentication status
  onAuthStateChanged(auth, (user) => {
    isAuthenticated.value = !!user;
    if (user) {
      fetchUserSubscription(user.uid);
    }
  });
  
  await fetchPlans()
})

const startPlan = async (planId) => {
  if (!isAuthenticated.value) {
    Swal.fire({
      title: 'Authentication Required',
      text: 'Please log in to subscribe to a plan',
      icon: 'warning',
      confirmButtonText: 'Go to Login',
      showCancelButton: true,
      cancelButtonText: 'Cancel'
    }).then((result) => {
      if (result.isConfirmed) {
        router.push('/login')
      }
    })
    return
  }

  // Redirect to the payment page for the specific plan
  router.push(`/subscription-plan/${planId}`)
}
</script> 